blockHeight('.products-block--lg', 991);
blockHeight('.products-block--lg h3', 991);
blockHeight('.products-block--sm', 567);
blockHeight('.products-block--sm h3', 567);
blockHeight('.products-block--sm h4', 567);

$('.products-block a').hover(
	function(){
		$(this).parents('.products-block').addClass('products-block--shadow');
	},

	function(){
		$(this).parents('.products-block').removeClass('products-block--shadow');
	}
);