$(function(){
	$('.companies-slider').slick({
		slidesToShow: 5,
		slidesToScroll: 1,
		infinite: true,
		prevArrow: '<button type="button" class="slick-prev"><i class="zmdi zmdi-chevron-left"></i></button>',
		nextArrow: '<button type="button" class="slick-next"><i class="zmdi zmdi-chevron-right"></i></button>',
		responsive: [
		{
			breakpoint: 1199,
			settings: {
				slidesToShow: 4
			}
		},
		{
			breakpoint: 768,
			settings: {
				slidesToShow: 3
			}
		},
		{
			breakpoint: 576,
			settings: {
				slidesToShow: 2
			}
		}
		]
	});
})